import app from '@/common/app.js'; //公共变量
import util from '@/common/util.js';
import request from '@/common/request.js';
import {
    mapMutations
} from 'vuex';
export default {
    data() {
        return {
            username: '',
            password: '',
            // username: '17629261881',
            // password: 'suyupeng',                
            comeFrom: app.COME_FROM,
            note: "登录",
            disabled: false,
            updatePassDisable: false,
            openid: ''
        }
    },
    onShow() {
      this.getOpenId();  
    },
    methods: {
        ...mapMutations(['login', 'setUserInfo']),
        userlogin(event) {
            //util.getStatPoint('click','EVENT_DESCRIBE_4');
            if (this.username.length == 0) {
                this.showpageToast("请输入账号");
                return false;
            } else if (!/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(19[0-9]{1})|(17[0-9]{1}))+\d{8})$/.test(this.username)) {
                this.showpageToast("请输入正确的手机号");
                return false;
            } else if (this.password.length == 0) {
                this.showpageToast("请输入密码");
                return false;
            } else if (this.password.length < 6) {
                this.showpageToast("密码最短为6位");
                return false;
            } else {
                this.disabled = true;
                this.note = "登录中";
                this.password = this.password
            }
            uni.request({
                url: app.serverPath + '/app/login',
                method: 'POST',
                header: {
                    'content-type': 'application/x-www-form-urlencoded'
                },
                data: {
                    username: this.username,
                    password: util.Encrypt(app.AES_KEY, this.password),
                    comeFrom: this.comeFrom,
                    openid: this.openid
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.login(res.data.data);
                        this.setUserInfo(res.data.data.userInfo);
                        //判断是否领卡， 关闭当前页跳转
                        uni.reLaunch({
                            url: "/pages/tabbar/index/index"
                        })
                    } else {
                        this.disabled = false;
                        this.note = "登录";
                        this.showpageToast(res.data.msg);
                    }
                },
                fail: (res) => {
                    // this.showpageToast(res.data.msg);
                    this.showpageToast("请求错误");
                    this.disabled = false;
                }
            })

        },
        getOpenId() {
            if (this.openid == '') {
                uni.getProvider({
                    service: 'oauth',
                    success: (res) => {
                        console.log(res.provider)
                        if (~res.provider.indexOf('weixin')) {
                            uni.login({
                                provider: 'weixin',
                                success: (loginRes) => {
                                    console.log(JSON.stringify(loginRes));
                                    this.WxCode = loginRes.code;
                                    console.log(this.code)
                                    //获取用户openid
                                    request.http({
                                        url: "/app/pay/getInfo",
                                        data: {
                                            'code': this.WxCode
                                        },
                                        header: 'application/x-www-form-urlencoded',
                                        success: (res) => {
                                            this.openid = res.data.data.openId;
                                        },
                                        fail: () => {}
                                    })

                                }
                            });
                        }
                    }
                });
            }
        },
        showpageToast(msg) {
            this.disabled = false;
            uni.showToast({
                icon: 'none',
                title: msg
            });
        },
        nologin(event) {
            console.log("点击没有电子卡跳转到申领界面");
            uni.navigateTo({
                url: "./nocard"
            })
        },
        accInput(e) {
            this.account = e.target.value;
        },
        psdInput(e) {
            this.password = e.target.value;
        }
    }
}
